import http from "./httpService";
const devUrl = "";

export function getRates() {
  const url = devUrl + "/api/currencies/getRates";
  return http.get(url, { withCredentials: true });
}

export function getCurrencies() {
  const url = devUrl + "/api/currencies/getCurrencies";
  return http.get(url, { withCredentials: true });
}

export function getInitialCurrencies() {
  const url = devUrl + "/api/currencies/updateCurrencies";
  return http.get(url, { withCredentials: true });
}

export function updateCurrencies() {
  const url = devUrl + "/api/currencies/updateCurrencies";
  return http.get(url, { withCredentials: true });
}
export function addRate(rate) {
  const url = devUrl + "/api/currencies/addRate";
  return http.post(
    url,
    {
      currencyName: rate.rateName,
      currencyValue: rate.rateValue,
      buyCashRate: rate.buyCashRate,
      sellCashRate: rate.sellCashRate,
      buyTransferRate: rate.buyTransferRate,
      sellTransferRate: rate.sellTransferRate
    },
    { withCredentials: true }
  );
}

export function deleteRate(name) {
  const url = devUrl + "/api/currencies/deleteRate";
  return http.post(url, { rateName: name }, { withCredentials: true });
}

export function updateRate(rate) {
  const url = devUrl + "/api/currencies/updateRate";
  return http.post(
    url,
    {
      currencyName: rate.currencyName,
      buyCashRate: rate.buyCashRate,
      sellCashRate: rate.sellCashRate,
      buyTransferRate: rate.buyTransferRate,
      sellTransferRate: rate.sellTransferRate,
      isInTable: rate.isInTable
    },
    { withCredentials: true }
  );
}
