export const RATES_LOAD_REQUEST = "RATES_LOAD_REQUEST";
export const RATES_LOAD_SUCCESS = "RATES_LOAD_SUCCESS";
export const RATES_LOAD_FAIL = "RATES_LOAD_FAIL";
export const RESET_GET_RATES = "RESET_GET_RATES";

export const ratesLoadRequest = () => ({
  type: RATES_LOAD_REQUEST
});

export const ratesLoadSuccess = userRates => ({
  type: RATES_LOAD_SUCCESS,
  payload: userRates
});

export const ratesLoadFail = error => ({
  type: RATES_LOAD_FAIL,
  payload: error
});

export const resetGetRates = () => ({
  type: RESET_GET_RATES
});
