export const CURRENCIES_LOAD_REQUEST = "CURRENCIES_LOAD_REQUEST";
export const CURRENCIES_UPDATE_REQUEST = "CURRENCIES_UPDATE_REQUEST";
export const CURRENCIES_LOAD_SUCCESS = "CURRENCIES_LOAD_SUCCESS";
export const CURRENCIES_UPDATE_SUCCESS = "CURRENCIES_UPDATE_SUCCESS";
export const INITIAL_CURRENCIES_UPDATE_SUCCESS =
  "INITIAL_CURRENCIES_UPDATE_SUCCESS";
export const CURRENCIES_LOAD_FAIL = "CURRENCIES_LOAD_FAIL";
export const RESET_CURRENCIES = "RESET_CURRENCIES";

export const currenciesLoadRequest = () => ({
  type: CURRENCIES_LOAD_REQUEST
});

export const currenciesLoadSuccess = currencies => ({
  type: CURRENCIES_LOAD_SUCCESS,
  payload: currencies
});

export const currenciesLoadFail = error => ({
  type: CURRENCIES_LOAD_FAIL,
  payload: error
});

export const currenciesUpdateRequest = () => ({
  type: CURRENCIES_UPDATE_REQUEST
});

export const currenciesUpdateSuccess = () => ({
  type: CURRENCIES_UPDATE_SUCCESS
});

export const InitialcurrenciesUpdateSuccess = () => ({
  type: INITIAL_CURRENCIES_UPDATE_SUCCESS
});

export const resetCurrencies = () => ({
  type: RESET_CURRENCIES
});
