import { call, put } from "redux-saga/effects";
import {
  userLoginSuccess,
  userLoginFail,
  userLoadSuccess,
  userLoadFail,
  logoutSuccess,
  autoLoginFail,
  autoLoginSuccess,
  resetUserActions
} from "../Actions/UserActions/userActions";
import {
  forgotPasswordFail,
  forgotPasswordSuccess,
  resetForgotPassword
} from "../Actions/UserActions/forgotPasswordActions";
import {
  login,
  logout,
  getUser,
  autoLogin,
  forgotPassword
} from "../Services/userService";

import {
  ratesLoadSuccess,
  resetGetRates
} from "../Actions/MarketDataActions/Rates/getRatesActions";
import { resetRates } from "../Actions/MarketDataActions/Rates/addRateActions";
import { resetUpdateRate } from "../Actions/MarketDataActions/Rates/updateRateActions";
import { resetDeleteRateProps } from "../Actions/MarketDataActions/Rates/deleteRateActions";

import { getInitialCurrencies } from "../Services/marketService";
import { InitialcurrenciesUpdateSuccess } from "../Actions/MarketDataActions/Currencies/getCurrenciesActions";

import { resetDeleteProps } from "../Actions/AdminActions/deleteUserActions";
import { resetGetUsers } from "../Actions/AdminActions/getUsersActions";
import { resetRegisterUser } from "../Actions/AdminActions/registerUserActions";
import { resetUpdateUser } from "../Actions/AdminActions/updateUserActions";
import { resetCurrencies } from "../Actions/MarketDataActions/Currencies/getCurrenciesActions";

export function* loginUserSaga({ payload }) {
  try {
    yield call(login, payload);
    yield put(userLoginSuccess());
  } catch (error) {
    yield put(userLoginFail(error));
  }
}

export function* logoutUserSaga() {
  try {
    yield call(logout);
    yield put(resetDeleteProps());
    yield put(resetGetUsers());
    yield put(resetRegisterUser());
    yield put(resetUpdateUser());
    yield put(resetCurrencies());
    yield put(resetUserActions());
    yield put(resetForgotPassword());
    yield put(resetGetRates());
    yield put(resetRates());
    yield put(resetUpdateRate());
    yield put(resetDeleteRateProps());
    yield put(logoutSuccess());
  } catch (error) {}
}

export function* loadUserSaga() {
  try {
    yield call(getInitialCurrencies);
    const { data } = yield call(getUser);
    yield put(userLoadSuccess(data));
    yield put(InitialcurrenciesUpdateSuccess());
    yield put(ratesLoadSuccess(data.currenciesRates));
  } catch (error) {
    yield put(userLoadFail(error));
  }
}

export function* autoLoginSaga() {
  try {
    const { data } = yield call(autoLogin);
    yield put(autoLoginSuccess(data));
  } catch (error) {
    yield put(autoLoginFail(error));
  }
}

export function* forgotPasswordSaga({ payload }) {
  try {
    yield call(forgotPassword, payload);
    yield put(forgotPasswordSuccess());
  } catch (error) {
    yield put(forgotPasswordFail(error));
  }
}
