import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles } from "@material-ui/core";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import {
  currenciesLoadRequest,
  currenciesUpdateRequest
} from "../../../Redux/Actions/MarketDataActions/Currencies/getCurrenciesActions";
import { updateRateRequest } from "../../../Redux/Actions/MarketDataActions/Rates/updateRateActions";
import { ratesLoadRequest } from "../../../Redux/Actions/MarketDataActions/Rates/getRatesActions";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class TableManagement extends Component {
  state = {
    open: false,
    loadData: ""
  };

  componentDidMount() {
    if (this.props.LoggedIn) {
      this.interval = setInterval(() => {
        this.loadData();
      }, 60000);
    } else {
      this.props.history.push("/loginPage");
    }
  }

  loadData() {
    this.props.currenciesUpdateRequest();
    this.props.currenciesLoadRequest();
    this.props.ratesLoadRequest();
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };

  handleChoise = chosenRate => {
    chosenRate.isInTable = !chosenRate.isInTable;
    this.props.updateRateRequest(chosenRate);
    this.handleToggle();
  };

  render() {
    const { rates } = this.props; // if the page doesnt render properly we should add loadedRates...
    return (
      <Fragment>
        <ToastContainer />
        {
          <Fragment>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={() => this.handleToggle()}
            >
              add/delete
            </Button>
            <Menu
              id="simple-menu"
              open={this.state.open}
              onClose={() => this.handleToggle()}
            >
              {rates.map(rate => (
                <MenuItem
                  key={rate._id}
                  onClick={() => this.handleChoise(rate)}
                >
                  {rate.currencyName}
                </MenuItem>
              ))}
            </Menu>

            <table>
              <thead>
                <tr>
                  <th rowSpan="2">flag</th>
                  <th rowSpan="2">currency</th>
                  <th rowSpan="2">value</th>
                  <th colSpan="2" scope="colgroup">
                    cash
                  </th>
                  <th colSpan="2" scope="colgroup">
                    transfer
                  </th>
                </tr>
                <tr>
                  <th>buy </th>
                  <th>sell </th>
                  <th>buy </th>
                  <th>sell </th>
                </tr>
              </thead>
              <tbody>
                {rates.map(
                  rate =>
                    rate.isInTable && (
                      <tr className="col col-lg-4 grid-wrapper" key={rate._id}>
                        <td> </td>
                        <td> {rate.currencyName}</td>
                        <td> {rate.currencyValue}</td>
                        <td> {rate.buyCashRate + rate.currencyValue}</td>
                        <td> {rate.sellCashRate + rate.currencyValue}</td>
                        <td> {rate.buyTransferRate + rate.currencyValue}</td>
                        <td> {rate.sellTransferRate + rate.currencyValue}</td>
                      </tr>
                    )
                )}
              </tbody>
            </table>
          </Fragment>
        }
      </Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  updateRateRequest: rate => dispatch(updateRateRequest(rate)),
  currenciesLoadRequest: () => dispatch(currenciesLoadRequest()),
  currenciesUpdateRequest: () => dispatch(currenciesUpdateRequest()),
  ratesLoadRequest: () => dispatch(ratesLoadRequest())
});

const mapStateToProps = state => {
  return {
    rates: state.getRatesReducer.rates,
    loadedCurrencies: state.getCurrenciesReducer.loadedCurrencies,
    currencies: state.getCurrenciesReducer.currencies,
    LoggedIn: state.userReducer.LoggedIn,
    updateRateSuccess: state.updateRateReducer.updateRateSuccess,
    desiredRate: state.updateRateReducer.desiredRate
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(TableManagement));
