import React, { Fragment, Component } from "react";
import { connect } from "react-redux";
import { Button, withStyles, TextField } from "@material-ui/core";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";

const styles = () => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: 100,
    marginRight: 100,
    width: 200
  },
  menu: {
    width: 200
  }
});

class Calculator extends Component {
  state = {
    openCur: false,
    openRate: false,
    openCalc: false,
    showAction: false,
    currency: {
      currencyName: "",
      value: ""
    },
    rateName: "",
    rateValue: 0.0,
    manualRate: 0.0,
    amount: 0.0
  };

  openCalc = () => {
    this.setState({ openCalc: !this.state.openCalc });
  };

  handleCurrencyToggle = () => {
    this.setState({ openCur: !this.state.openCur });
  };

  handleRateToggle = () => {
    this.setState({ openRate: !this.state.openRate });
  };

  handleCurrencyChoise = (currencyName, currValue, isFavorite) => {
    let currencyValue = parseFloat(currValue);
    let checkRate = false;

    if (!isFavorite) {
      this.props.rates.forEach(rate => {
        if (rate.currencyName === currencyName) {
          checkRate = true;
        }
      });

      this.handleCurrencyToggle();
    } else {
      checkRate = true;
    }

    this.setState({
      showAction: checkRate,
      amount: 0.0,
      manualRate: 0.0,
      currency: {
        ...this.state.currency,
        currencyName: currencyName,
        value: currencyValue
      }
    });
  };

  handleRateChoise = rateAction => {
    const { currency } = this.state;

    this.props.rates.forEach(rate => {
      if (rate.currencyName === currency.currencyName) {
        this.setState({
          rateName: rateAction,
          rateValue: rate[rateAction]
        });
      }
    });

    this.handleRateToggle();
    return;
  };

  handleChange = ({ target: input }) => {
    this.setState({
      [input.name]: input.value
    });
  };

  render() {
    const { classes, currencies, rates } = this.props;
    const {
      openCalc,
      openCur,
      openRate,
      currency,
      amount,
      manualRate,
      rateName,
      rateValue
    } = this.state;
    return (
      <Fragment>
        <Button onClick={() => this.openCalc()}>Calculator</Button>
        {openCalc && (
          <Fragment>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={() => this.handleCurrencyToggle()}
            >
              {currency.currencyName || "currency"}
            </Button>
            <Menu
              id="simple-menu"
              open={openCur}
              onClose={() => this.handleCurrencyToggle()}
            >
              {Object.keys(currencies).map((currency, i) => (
                <MenuItem
                  key={i}
                  onClick={() =>
                    this.handleCurrencyChoise(
                      currency,
                      currencies[currency],
                      false
                    )
                  }
                >
                  {currency}
                </MenuItem>
              ))}
            </Menu>

            {rates.map(rate => (
              <Button
                key={rate._id}
                onClick={() =>
                  this.handleCurrencyChoise(
                    rate.currencyName,
                    rate.currencyValue,
                    true
                  )
                }
              >
                {rate.currencyName}
              </Button>
            ))}

            {currency.currencyName && (
              <Fragment>
                <div>{currency.value || "0"}</div>

                <TextField
                  className={classes.textField}
                  autoFocus
                  name="manualRate"
                  label="manual rate"
                  type="manualRate"
                  value={manualRate}
                  onChange={this.handleChange}
                  fullWidth
                  required
                />
                <TextField
                  className={classes.textField}
                  autoFocus
                  name="amount"
                  label="amount"
                  type="amount"
                  value={amount}
                  onChange={this.handleChange}
                  fullWidth
                  required
                />
              </Fragment>
            )}

            {this.state.showAction && (
              <Fragment>
                <Button
                  aria-controls="simple-menu-2"
                  aria-haspopup="true"
                  onClick={() => this.handleRateToggle()}
                >
                  {rateName || "action"}
                </Button>
                <Menu
                  id="simple-menu-2"
                  open={openRate}
                  onClose={() => this.handleRateToggle()}
                >
                  <MenuItem
                    onClick={() => this.handleRateChoise("buyCashRate")}
                  >
                    buy cash
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("sellCashRate")}
                  >
                    sell cash
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("buyTransferRate")}
                  >
                    buy by transfer
                  </MenuItem>
                  <MenuItem
                    onClick={() => this.handleRateChoise("sellTransferRate")}
                  >
                    sell by transfer
                  </MenuItem>
                </Menu>
              </Fragment>
            )}
            <div>
              {(Number(currency.value) + rateValue + Number(manualRate)) *
                amount}
            </div>
          </Fragment>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    rates: state.getRatesReducer.rates,
    currencies: state.getCurrenciesReducer.currencies
  };
};

export default connect(mapStateToProps, null)(withStyles(styles)(Calculator));
